//
// Icons
// --------------------------------------------------

.lp-icon, .lp-default-icon {
    font-family: @icon-font;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    position: relative;
    line-height: 1.2;
    top: .03em;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
a:hover i {
    text-decoration:none;
}

.lp-line-item-arrow {
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -9px;
    width: 11px;
    color: #bbb;
}
// TODO: change class name
.lp-icon-red {
    color:@brand-primary;
}

// Icon Sizes
// -------------------------

.lp-icon-small { font-size:@icon-size-small; }
.lp-icon-medium { font-size:@icon-size-medium; }
.lp-icon-large { font-size:@icon-size-large; }

.lp-icon-caret-up, .lp-default-icon-caret-up,
.lp-icon-caret-down, .lp-default-icon-caret-down,
.lp-icon-caret-right, .lp-default-icon-caret-right,
.lp-icon-caret-left, .lp-default-icon-caret-left {
    font-size: @icon-size-small;
}

